import { Component,  OnInit, OnDestroy, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { GridOptions } from 'ag-grid-community';
import * as moment from 'moment';
import { SearchCallService } from '@app/services';
import { CallTransactionService } from '../../services/call/transaction.service';
import { ColumnActionRenderer } from './renderer/column-action-renderer.component';
import { ColumnCallidRenderer } from './renderer/column-callid-renderer.component';
import { ColumnMethodRenderer } from './renderer/column-method-renderer.component';
import { HeaderActionRenderer } from './renderer/header-action-renderer.component';
import { Subscription } from 'rxjs';
import { Functions } from '../../helpers/functions';
import { DateTimeRangeService, DateTimeTick } from '../../services/data-time-range.service';

@Component({
    selector: 'app-search-grid-call',
    templateUrl: './search-grid-call.component.html',
    styleUrls: ['./search-grid-call.component.css']
})
export class SearchGridCallComponent implements OnInit, OnDestroy, AfterViewInit {
    private gridApi;
    private gridColumnApi;
    public context;
    public frameworkComponents;
    filterGridValue: string;
    defaultColDef: Object;
    columnDefs: Array<Object>;
    myPredefColumns: Array<Object>;
    rowData: Object;
    showPortal = false;
    isOpenDialog = false;
    title = 'Call Result';
    isLoading = false;
    activeRow = '';

    arrWindow: Array<any> = [];
    arrMessageDetail: Array<any> = [];

    gridOptions: GridOptions = <GridOptions> {
        defaultColDef: {
            sortable: true,
            resizable: true
        },
        rowHeight: 38,
        rowSelection: 'multiple',
        suppressRowClickSelection: true,
        getRowStyle: this.getBkgColorTable.bind(this),
        suppressCellSelection: true
    };
    protocol_profile: string;
    config = {
        param: {
            transaction: {},
            limit: 200,
            search: {},
            location: {},
            timezone: {
                value: -180,
                name: 'Local'
            }
        },
        timestamp: { from: 0, to: 0 }
    };
    private _interval: any;
    private subscriptionRangeUpdateTimeout: Subscription;

    constructor(
        private _scs: SearchCallService,
        private _cts: CallTransactionService,
        private _dtrs: DateTimeRangeService,
        private changeDetectorRefs: ChangeDetectorRef
    ) {
        this.columnDefs = [];
        this.myPredefColumns = [
            {   headerName: '', 
                field: '', 
                minWidth: 60, 
                maxWidth: 60, 
                //cellRenderer: 'columnActionRenderer',
                checkboxSelection: true,
                cellRendererParams: { checkbox: true },
                pinned: 'left', 
                cellClass: 'no-border', 
                headerComponentFramework: HeaderActionRenderer
            },
        ];
        this.context = { componentParent: this };
        this.frameworkComponents = {
            columnActionRenderer: ColumnActionRenderer,
            columnCallidRenderer: ColumnCallidRenderer,
            columnMethodRenderer: ColumnMethodRenderer
        };
    }

    ngOnInit() {
        this.subscriptionRangeUpdateTimeout = this._dtrs.castRangeUpdateTimeout.subscribe(() => this.update());
    }
    update() {
        console.log('== update() ==');

        this.config.timestamp = this._dtrs.getDatesForQuery(true);

        if (localStorage.getItem('searchQuery')) {
            const localData = JSON.parse(localStorage.getItem('searchQuery'));
            this.protocol_profile = localData.protocol_id;
            this.config.param.search[localData.protocol_id] = localData.fields;
        }

        /* this is normaly not needed - just a workaround to copy from search param */
        for( let item in this.config.param.search) {            
            let elem = this.config.param.search[item];
            if(elem.filter(it => it.name === 'limit').length > 1)                
            {                
                this.config.param.limit = parseInt(elem.filter(it => it.name === 'limit')[0]['value']);
            }
        }
        
        const getDataSubscribe = this._scs.getData(this.config).subscribe(result => {
            getDataSubscribe.unsubscribe();
            console.log('==this._scs.getData(this.config).subscribe==', result);
            const myColumnDefs = [
                { headerName: 'ID', field: 'id', minWidth: 20, maxWidth: 40, hide: true},
                { headerName: 'Date', field: 'create_date', filter: true, suppressSizeToFit: true,
                    valueFormatter: data => data.value ? moment(data.value).format('YYYY-MM-DD HH:mm:ss.SSS') : null},
                { headerName: 'SID', field: 'sid', filter: true, resizable: true,
                    cellRenderer: 'columnCallidRenderer', cellStyle: this.getCallIDColor.bind(this)},
                { headerName: 'Call-ID', field: 'callid', filter: true, resizable: true,
                    cellRenderer: 'columnCallidRenderer', cellStyle: this.getCallIDColor.bind(this)},
                { headerName: 'Method', field: 'method', filter: true, cellRenderer: 'columnMethodRenderer',
                    resizable: true, cellStyle: this.getMethodColor.bind(this)},
                { headerName: 'From User', field: 'from_user', filter: true, resizable: true},
                { headerName: 'RURI User', field: 'ruri_user', filter: true, resizable: true},
                { headerName: 'TO User', field: 'to_user', filter: true, resizable: true},
                { headerName: 'Source IP', field: 'srcIp', filter: true, resizable: true},
                { headerName: 'Destination IP', field: 'dstIp', filter: true, resizable: true},
                { headerName: 'Protocol', field: 'protocol', filter: true, resizable: true},
                { headerName: 'Capture id', field: 'captureId', filter: true, resizable: true}
            ];

            this.columnDefs = this.myPredefColumns.concat(myColumnDefs);
            setTimeout(() => {
                this.sizeToFit();
            }, 100);
            this.rowData = result['data'];
            setTimeout(() => {
                this.sizeToFit();
            }, 200);
        });
    }
    ngAfterViewInit () {
        window.onresize = this.sizeToFit.bind(this);
        window.document.body.addEventListener('mouseup', evt => {
            this.sizeToFit();
            setTimeout(() => {
                this.sizeToFit();
            }, 500);
        });
    }

    hashCode(str) {
        let hash = 0;
        if (str) {
            for (let i = 0; i < str.length; i++) {
                hash = str.charCodeAt(i) + ((hash << 5) - hash);
            }
        }
        return hash;
    }

    intToARGB(i) {
        return ((i >> 24) & 0xFF);
    }

    getCallIDColor (params) {
        return {'color': '#' + Functions.getColorByString(params.value)};
    }

    getMethodColor (params) {
        return {'color': '#' + Functions.getColorByString(params.value)};
    }

    getBkgColorTable(params) {
        const callid = params.data.callid;
        const his = this.hashCode(callid);
        const color = 'hsl(' + this.intToARGB(his) + ', 75%, 85%, 0.3)';
        return {
            'background-color': color
        };
    }

    getSelectedRows() {
        const selectedNodes = this.gridApi.getSelectedNodes();
        const selectedData = selectedNodes.map( node => node.data );
        const selectedDataStringPresentation = selectedData.map( node => node.callid + ' ' + node.ruri_user).join(', ');
        this.showPortal = true;
    }

    sizeToFit() {
        if (this.gridApi) {
            this.gridApi.sizeColumnsToFit();
            //this.gridColumnApi.sizeColumnsToFit();
        }
    }

    autoSizeAll() {
        const allColumnIds: Array<any> = [];
        this.gridColumnApi.getAllColumns().forEach(function(column) {
            allColumnIds.push(column.colId);
        });
        this.gridColumnApi.autoSizeColumns(allColumnIds);
        this.sizeToFit();
    }
    setQuickFilter() {
        console.log(this.filterGridValue)
        this.gridApi.setQuickFilter(this.filterGridValue);
    }
    onGridReady(params) {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    openTransactionForSelectedRows(index, row) {
        const data = {data: row};
        this.openTransactionDialog(data);
    }

    openMethodForSelectedRow(index, row ) {
        const data = {data: row};
        this.addWindowMessage(data);
    }

    openTransactionDialog (row) {
        // do not open duplicate window
        if ((this.arrWindow.filter(i => i.data.data.sid[row.data.callid] != null)[0] != null)) {
            return;
        }

        const selectedRows = this.gridApi.getSelectedRows();

        /* clear from clones */
        const selectedCallId = selectedRows.map(i => i.callid).reduce((a, b) => {
            if (a.indexOf(b) === -1) {
                a.push(b);
            }
            return a;
        }, []);
        const color = '#' + Functions.getColorByString(row.data.callid);

        const request = {
            param: this.config.param || {} as any,
            timestamp: this.config.timestamp
        };
        request.param.search[this.protocol_profile] = {
            id: row.data.id,
            callid: selectedCallId.length > 0 ? selectedCallId : [row.data.callid],
            uuid: []
        };
        request.param.transaction = {
            call: this.protocol_profile === '1_call',
            registration: this.protocol_profile === '1_registration',
            rest: this.protocol_profile === '1_default'
        };

        const windowData = {
            loaded: false,
            data: null,
            id: row.data.sid,
            headerColor: color || ''
        };

        this.arrWindow.push(windowData);

        const getTransactionSubscription = this._cts.getTransaction(request).subscribe(data => {
            getTransactionSubscription.unsubscribe();
            windowData.loaded = true;
            windowData.data = data;
        });
    }

    closeWindow(id: number) {
        this.arrWindow.splice(id, 1);
    }

    addWindowMessage(row: any) {
        if ((this.arrMessageDetail.filter(i => i.id === row.data.id)[0] != null)) {
            return;
        }
        const color = '#' + Functions.getColorByString(row.data.method || 'LOG');
        const mData = {
            loaded: false,
            data: null,
            id: row.data.id,
            headerColor: color || '',
            isBrowserWindow: row.isBrowserWindow
        };

        if (row.isLog) {
            console.log('=== LOG ===', row);

            const data = row.data.item;
            mData.data = data;
            mData.data.item = {
                raw: mData.data.raw
            };
            mData.data.messageDetaiTableData = Object.keys(mData.data)
                .map(i => {
                    let val;
                    if (i === 'create_date') {
                        val = moment(mData.data[i]).format('DD-MM-YYYY');
                    } else if (i === 'timeSeconds') {
                        val = moment( mData.data[i]).format('hh:mm:ss.SSS');
                    } else {
                        val = mData.data[i];
                    }
                    return {name: i, value: val};
                })
                .filter(i => typeof i.value !== 'object' && i.name !== 'raw');
            this.changeDetectorRefs.detectChanges();
            mData.loaded = true;
            this.arrMessageDetail.push(mData);
            return;
        }


        const request = {
            param: this.config.param || {} as any,
            timestamp: this.config.timestamp
        };

        request.param.limit = 1;
        request.param.search[this.protocol_profile] = { id: row.data.id };
        request.param.transaction = {
            call: this.protocol_profile === '1_call',
            registration: this.protocol_profile === '1_registration',
            rest: this.protocol_profile === '1_default'
        };



        this.arrMessageDetail.push(mData);

        const getMessageSubscription = this._scs.getMessage(request).subscribe(data => {
            getMessageSubscription.unsubscribe();
            console.log('get message data::', data);

            mData.data = data.data[0];
            mData.data.item = {
                raw: mData.data.raw
            };
            mData.data.messageDetaiTableData = Object.keys(mData.data)
                .map(i => {
                    let val;
                    if (i === 'create_date') {
                        val = moment(mData.data[i]).format('DD-MM-YYYY');
                    } else if (i === 'timeSeconds') {
                        val = moment( mData.data[i]).format('hh:mm:ss.SSS');
                    } else {
                        val = mData.data[i];
                    }
                    return {name: i, value: val}
                })
                .filter(i => typeof i.value !== 'object' && i.name !== 'raw');
            this.changeDetectorRefs.detectChanges();
            mData.loaded = true;
        });
    }

    closeWindowMessage(id: number) {
        this.arrMessageDetail.splice(id, 1);
    }

    ngOnDestroy () {
        this.subscriptionRangeUpdateTimeout.unsubscribe();
        clearInterval(this._interval);
    }
}
