// GRIDSTER & ANGULAR
import { Component, OnInit, ViewEncapsulation, ViewChildren, QueryList, OnDestroy, AfterViewInit } from '@angular/core';
import { GridsterConfig, GridType, DisplayGrid } from 'angular-gridster2';
import { ActivatedRoute, Router } from '@angular/router';
import { DashboardModel, DashboardContentModel } from '@app/models';
import { DashboardService } from '@app/services';
import { MatDialog } from '@angular/material/dialog';
import { DeleteDialogComponent } from './delete-dialog/delete-dialog.component';
import { AddDialogComponent } from './add-dialog/add-dialog.component';
import { EditDialogComponent } from './edit-dialog/edit-dialog.component';
import { IWidget } from '../widgets/IWidget';
// import { Subscription } from 'rxjs';


@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.css'],
    encapsulation: ViewEncapsulation.None
})
export class DashboardComponent implements OnInit, OnDestroy, AfterViewInit {
    // static dashboardId: string;
    gridOptions: GridsterConfig;
    dashboardTitle: string;
    dashboardCollection: DashboardModel;
    dashboardArray: DashboardContentModel[];
    isIframe = false;
    iframeUrl: string;
    @ViewChildren('widgets') widgets: QueryList<IWidget>;

    constructor (
        private _route: ActivatedRoute,
        private _ds: DashboardService,
        private router: Router,
        public dialog: MatDialog) {}

    ngOnInit() {
        // Grid options
        this.gridOptions = {
            gridType2: GridType.VerticalFixed,
            gridType: GridType.Fit,
            enableEmptyCellDrop: true,
            // emptyCellDropCallback: this.onDrop,
            useTransformPositioning: true,

            mobileBreakpoint: 640,
            disableWindowResize: false,
            swap: true,
            swapWhileDragging: true,
            margin: 10,
            scrollSensitivity: 10,
            scrollSpeed: 20,
            resizable: {
                enabled: true
            },
            itemChangeCallback: this.itemChange.bind(this),
            itemResizeCallback: this.itemChange.bind(this),
            draggable: {
                enabled: true,
                ignoreContent: true,
                dropOverItems: true,
                dragHandleClass: 'drag-handler',
                ignoreContentClass: 'no-drag',
            },
            /* Pushig elements */
            pushItems: true,
            disablePushOnDrag: false,
            disablePushOnResize: false,
            pushResizeItems: true,
            pushDirections: { north: true, east: true, south: true, west: true },

            /*  */
            scrollToNewItems: true,
            displayGrid: DisplayGrid.OnDragAndResize,
            minCols: 5,
            maxCols: 5,
            minRows: 5,
            maxRows: 5,
        };
        this.getData();
    }
    ngAfterViewInit () {
        this.updateTrigger();
    }
    updateTrigger() {
        setTimeout(() => {
            const elList = document.body.querySelectorAll('.gridster-item-resizable-handler.ng-star-inserted');

            Array.from(elList).forEach((i: any) => {
                i.onmouseup = window.document.body.onmouseleave = e => {
                    Array.from(document.body.querySelectorAll('.widget-block .shadow-polygon')).forEach( (j: any) => {
                        j.style.display = 'none';
                    });
                };
                i.onmousedown = e => {
                    const el = e.target.parentElement.querySelector('.widget-block');
                    if (el.id.indexOf('iframe') !== -1) {
                        el.querySelector('.shadow-polygon').style.display = 'block';
                    }
                };
            });
        }, 500);
    }
    onEvent (e) {
        console.log(e);
        return true;
    }

    getData() {
        this._route.params.subscribe(params => {
            this._ds.setCurrentDashBoardId(params['id']);
            const dashboardStoreSubscription = this._ds.getDashboardStore(this._ds.getCurrentDashBoardId()).subscribe(dashboard => {
                this.dashboardCollection = dashboard;
                this.isIframe = this.dashboardCollection.data.type === 2;
                if (this.isIframe) {
                    this.iframeUrl = this.dashboardCollection.data.param;
                }
                this.dashboardTitle = this.dashboardCollection &&
                    this.dashboardCollection.data ?
                        this.dashboardCollection.data.name : '';

                if (this.dashboardCollection) {
                    ((g, c) => {
                        g.maxRows = g.minRows = c.maxrows;
                        g.maxCols = g.minCols = c.columns;

                        g.pushItems = c.pushing;
                        g.disablePushOnDrag = !c.pushing;
                        g.disablePushOnResize = !c.pushing;
                        g.pushResizeItems = c.pushing;

                    })(this.gridOptions, this.dashboardCollection.data.config);

                    this.changedOptions();

                    const widgets = this.dashboardCollection.data.widgets;
                    // sync old version and new version
                    this.dashboardArray = widgets.map( (item, i) => {
                        item.id = item.name + '' + i;
                        item.cols = item.cols || item.sizeX;
                        item.rows = item.rows || item.sizeY;
                        item.sizeX = item.sizeX || item.cols;
                        item.sizeY = item.sizeY || item.rows;

                        item.x = item.x || item.col;
                        item.y = item.y || item.row;
                        return item;
                    });
                } else {
                    this.dashboardArray = [];
                }
                this.dashboardCollection.data.widgets = this.dashboardArray;
                this.updateTrigger();
                dashboardStoreSubscription.unsubscribe();
            });
        });
    }
    onStartChange(e) {
        console.log('onStartChange::', e);
    }
    onStopChange(e) {
        console.log('onStopChange::', e);
    }
    itemChange(item: any) {
        setTimeout(() => {
            const subscription = this.onDashboardSave().subscribe(() => {
                subscription.unsubscribe();
                if (item.name === 'iframe') {
                    this.getWidgetById(item.id).refresh();
                }
            });
        }, 10);
        return true;
    }

    changedOptions() {
        this.gridOptions.api.optionsChanged();
    }

    openSettings(item: any) {
        this.getWidgetById(item.id).openDialog();
    }

    removeItem($event: any, item: any) {
        $event.preventDefault();
        $event.stopPropagation();

        this.dashboardArray = this.dashboardArray.filter( i => JSON.stringify(i) !== JSON.stringify(item));

        setTimeout(() => {
            const subscription = this.onDashboardSave().subscribe(() => {
                subscription.unsubscribe();
            });
        }, 10);
    }

    getWidgetById(id: string): IWidget {
        return this.widgets.filter(i => i['id'] === id)[0] as IWidget;
    }

    onChangeWidget(config: any, target: any) {
        this.dashboardArray[this.dashboardArray.map(i => i.id).indexOf(target.id)].config = config;
        target.title = config.title;

        setTimeout(() => {
            const subscription = this.onDashboardSave().subscribe(() => {
                subscription.unsubscribe();
            });
        }, 10);
    }

    onDashboardAdd() {
        const dialogRef = this.dialog.open(AddDialogComponent, {
            width: '650px',
            data: {}
        });

        const dialogRefSubscription = dialogRef.afterClosed().subscribe( name => {
            if (name) {
                const widget: DashboardContentModel = {
                    x: 0, y: 0, cols: 1, rows: 1,
                    name: name, title: name,
                    id: name + (Math.random() * 1000).toFixed(0)
                };
                this.dashboardArray.push(widget);

                const subscription = this.onDashboardSave().subscribe(() => {
                    subscription.unsubscribe();
                });
                this._ds.update();
                dialogRefSubscription.unsubscribe();
            }
        });
    }

    onDashboardSettings() {
        this.dashboardCollection.data = this.dashboardCollection.data || {};
        this.dashboardCollection.data.config = this.dashboardCollection.data.config || {};

        const dialogRef = this.dialog.open(EditDialogComponent, {
            width: '650px',
            data: {
                name: this.dashboardTitle,
                type: this.dashboardCollection.data.type,
                param: this.dashboardCollection.data.param || '',
                shared: !!this.dashboardCollection.data.shared,
                columns: this.dashboardCollection.data.config.columns || 1,
                maxrows: this.dashboardCollection.data.config.maxrows || 1,
                pushing: !!this.dashboardCollection.data.config.pushing
            }
        });
        const dialogRefSubscription = dialogRef.afterClosed().subscribe( (data) => {
            if (data) {
                ((d, dd) => {
                    d.param = data.param;
                    dd.param = data.param;
                    dd.name = data.name;
                    dd.type = data.type;
                    dd.shared = data.shared;
                    dd.config.columns = data.columns;
                    dd.config.maxrows = data.maxrows;
                    dd.config.pushing = data.pushing;

                    ((g, c) => {
                        g.maxRows = g.minRows = c.maxrows;
                        g.maxCols = g.minCols = c.columns;
                        g.pushItems = c.pushing;
                        g.disablePushOnDrag = !c.pushing;
                        g.disablePushOnResize = !c.pushing;
                        g.pushResizeItems = c.pushing;

                    })(this.gridOptions, dd.config);
                    
                    this.changedOptions();

                })(this.dashboardCollection, this.dashboardCollection.data);

                const subscription = this.onDashboardSave().subscribe(() => {
                    subscription.unsubscribe();
                    this.getData();
                    this._ds.update();
                });
            }

            dialogRefSubscription.unsubscribe();
        });
    }

    onDashboarDelete() {
        const dialogRef = this.dialog.open(DeleteDialogComponent, {
            width: '350px',
            data: {}
        });

        const dialogRefSubscription = dialogRef.afterClosed().subscribe( (data) => {
            if (data) {
                const deleteDashboardStoreSubscription = this._ds.deleteDashboardStore(this.dashboardCollection.data.id).subscribe(() => {
                    deleteDashboardStoreSubscription.unsubscribe();
                    this.router.navigateByUrl('/');
                    this._ds.update();
                });
            }
            dialogRefSubscription.unsubscribe();
        });
    }

    onDashboardSave() {
        this.dashboardCollection.data.widgets = this.dashboardArray;
        return this._ds.postDashboardStore(this._ds.getCurrentDashBoardId(), this.dashboardCollection.data);
    }

    ngOnDestroy() {
        // this.subscription.unsubscribe();
    };
}
